$thumb-size: 150px;
$citation-height: 160px;
$wrap: 800px;

.citation-container {
  container-type: inline-size;
}

.citation {
  display: flex;
  margin: 15px 0;
  border-radius: var(--rounded);
  background: var(--background);
  overflow: hidden;
  box-shadow: var(--shadow);
  height: $citation-height;
}

.citation-image {
  width: $thumb-size;
  height: $citation-height;
  flex-shrink: 0;
  overflow: hidden;
  background: var(--light-gray);
}

.citation-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

.citation-text {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  gap: 8px;
  max-width: 100%;
  height: 100%;
  padding: 15px;
  padding-left: 20px;
  text-align: left;
  overflow-wrap: break-word;
  overflow: hidden;
  z-index: 0;
}

.citation-title,
.citation-authors,
.citation-details,
.citation-description {
  width: 100%;
}

.citation-title {
  font-weight: var(--semi-bold);
  font-size: 1.1em;
}

.citation-authors {
  font-size: 0.95em;
}

.citation-details {
  font-size: 0.9em;
  color: var(--gray);
}

.citation-text > .icon {
  position: absolute;
  top: 15px;
  right: 15px;
  color: var(--light-gray);
  opacity: 0.5;
  font-size: 24px;
  z-index: -1;
}

.citation-publisher {
  text-transform: capitalize;
}

.citation-description {
  color: var(--gray);
  font-size: 0.95em;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.citation-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.citation-buttons .button {
  margin: 0;
}

.citation-text > .tags {
  display: inline-flex;
  justify-content: flex-start;
  margin: 0;
}

@container (max-width: #{$wrap}) {
  .citation {
    flex-direction: column;
    height: auto;
  }

  .citation-image {
    width: unset;
    height: $thumb-size;
  }
  
  .citation-text {
    height: auto;
  }
}